<!--入学咨询答疑详细 -->
<template>
  <div class="enrolconsultdetail">
    <div class="enrolconsultdetailtitle">入学咨询答疑</div>
    <div style="width: 100%;height: 1px;background:#C8C8C8;margin-top: 20px;"></div>
    <div class="question">
      <div class="questioner">杨瑞</div>
      <div class="questiontime">2018-9-28: 16:40</div>
      <img class="headimg" src="@/assets/img/enrolconsult/headimg.png" style="border-radius:50px;height: 97px;position: relative;top: -120px;left: 35px;">
      <div class="questiontext">请问我女儿正上初二，数学比较差，有推荐的课程吗？请问我女儿正上初二，数学比较差，有推荐的课程吗？</div>
    </div>
    <div style="width: 100%;height: 1px;background:#C8C8C8;margin-top: 20px;"></div>
    <div class="question">
      <div class="questioner">暗中观察老师</div>
      <div class="questiontime">2018-9-28: 16:40</div>
      <img class="headimg" src="@/assets/img/enrolconsult/teacherimg.png" style="border-radius:50px;height: 97px;position: relative;top: -120px;left: 35px;">
      <div class="questiontext" v-html="answertext"></div>
    </div>
    <div class="editor" style="margin: 10px;">
      <vue-ueditor-wrap v-model="msg" :config="myConfig"></vue-ueditor-wrap>
      <div style="text-align:right;">
        <el-button type="danger" round style="font-size:16px;margin: 20px 0px;" @click="submit">提交</el-button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'enrolconsultdetail-view',
  data() {
    return {
      questionID: '',
      answertext: '您好，我们有数学精品课推荐给您',
      msg: '<p><span style="color: rgb(16, 16, 16); font-family: &quot;Microsoft YaHei&quot;; font-size: 28px;">您好，我们有数学精品课推荐给您</span></p>',
      myConfig: {
      // 编辑器不自动被内容撑高
        autoHeightEnabled: false,
        // 初始容器高度
        initialFrameHeight: 240,
        // 初始容器宽度
        initialFrameWidth: '100%',
        toolbars: [[
          'bold', // 加粗
          'italic', // 斜体
          'underline', // 下划线
          'simpleupload', // 单图上传
          'link', // 超链接
          'unlink', // 取消链接
          'insertorderedlist', // 有序列表
          'insertunorderedlist', // 无序列表
          'blockquote', // 引用
          'undo', // 撤销
          'redo', // 重做
          'insertframe', // 插入Iframe
          'source', // 源代码
        ]],
        // 上传文件接口（参考static/UEditor/jsp/controller.jsp！！！）
        serverUrl: 'http://35.201.165.105:8000/controller.php',
        // UEditor 资源文件的存放路径，如果你使用的是 vue-cli 生成的项目，通常不需要设置该选项，vue-ueditor-wrap 会自动处理常见的情况，如果需要特殊配置，参考下方的常见问题2
        // UEDITOR_HOME_URL: '/static/UEditor/',
      },
    }
  },
  created() {
    this.$emit('header', true);
    this.$emit('footer', false);
    if (this.$route.query.questionID) {
      this.questionID = this.$route.query.questionID;
    }
    document.onkeydown = () => {
      const key = window.event.keyCode;
      if (key === 13) {
        this.submit()
      }
    }
  },
  methods: {
    submit() {
      this.answertext = this.msg;
      this.msg = '';
    },
  },
}
</script>
<style scoped>
  .enrolconsultdetailtitle{
    margin-left: 35px;
    margin-top: 66px;
    width: 197px;
    height: 53px;
    line-height: 41px;
    color: rgba(16, 16, 16, 1);
    font-size: 28px;
    text-align: center;
    font-family: Microsoft Yahei;
  }
  .questioner{
    margin: 27px 0px 0px 164px;
    height: 63px;
    line-height: 41px;
    color: rgba(16, 16, 16, 1);
    font-size: 28px;
  }
  .questiontime{
    margin: 0px 0px 0px 164px;
    height: 53px;
    line-height: 29px;
    color: rgba(16, 16, 16, 1);
    font-size: 20px;
  }
  .questiontext{
    margin: -97px 58px 70px 58px;
    height: 53px;
    line-height: 41px;
    color: rgba(16, 16, 16, 1);
    font-size: 28px;
  }
</style>



